<template>
  <div :model="active">
    <canvas id="myChart"></canvas>
  </div>
</template>
<script>
const Chart = require('chart.js/dist/chart.js');

export default {
  name: "Chart",
  props: {
    xValues: Array,
    yValues: Array,
    MIN: Number,
    MAX: Number,
    color: String,
    active: Boolean
  },
  methods: {
    run: function () {
      new Chart("myChart", {
        type: "line",
        data: {
          labels: this.xValues,
          datasets: [{
            fill: false,
            lineTension: 1,
            backgroundColor: this.color,
            borderColor: "rgba(0,255,0,0.1)",
            data: this.yValues
          }]
        },
        options: {
          legend: {display: false},
          scales: {
            yAxes: [{ticks: {min: this.MIN, max:this.MAX}}],
          }
        }
      });
    }
  },
  watch: {
    active() {
      this.run()
    },
  },
}
</script>

